
<template>
    <header id="header">
        <h1>{{ title }}</h1>
    </header>
</template>

<script>
    export default {
        name: "Header",
        props: {
            title: {
                type: String,
                default: '交易平台'
            }
        },
    }
</script>

<style scoped>
#header {
  width: 80%;
  height: 50px;
  padding: 15px;
  color: #ffffff;
  background: #0c4469;
  border-bottom: 1px solid #0c4469;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  font-family: normal;
}

#header h1 {
  font-size: 30px;
  text-align: center;
  line-height: 50%;
  font-weight: normal;
  letter-spacing: 10px;
}

#header i {
  position: absolute;
  left: 5px;
  top: 50%;
  margin-top: -13px;
  font-size: 26px;
}
</style>



